.quote-box {
  width: 248px;
  padding: 15px 20px;
  background-color: white;
  font: 15px "Soho Gothic Pro", Arial, sans-serif; }
  @media screen and (max-width: 599px) {
    .quote-box {
      width: 100%;
      box-sizing: border-box; } }
  .w400 .quote-box {
    width: 100%;
    box-sizing: border-box; }
  .w320 .quote-box {
    width: 100%;
    box-sizing: border-box; }
  .quote-box__header {
    text-transform: uppercase;
    text-align: center;
    line-height: 100%;
    padding-right: 15px;
    color: #468fc5;
    font-weight: 700;
    margin-bottom: 29px;
    position: relative; }
    .quote-box__header:after {
      content: '';
      position: absolute;
      display: block;
      width: 100%;
      height: 35px;
      background-image: url("/img/line_with_circle.png");
      background-repeat: no-repeat;
      background-position: center; }

.quote-form__submit {
  font: 12.59px "Soho Gothic Pro";
  margin-top: 10px;
  text-align: right; }

.quote-form__links {
  font: 12px "Tahoma";
  margin-top: -7px;
  text-align: right; }

.quote-form-link {
  color: #336699; }

.form-group--pusher {
  padding-top: 5px; }

.form-group__label {
  font: 11px "Soho Gothic Pro";
  display: block;
  margin-bottom: 2px; }
  .form-group__label:after {
    content: ":"; }

.form-group__form-control {
  margin-bottom: 7px;
  font: 14px "Tahoma";
  line-height: 100%;
  display: block;
  width: 100%;
  box-sizing: border-box;
  border: 2px solid #c4c4c4;
  border-radius: 3px;
  padding: 7px 7px;
  outline: none; }
  .form-group__form-control--error {
    border-color: #e98c8c; }
  .form-group__form-control--focus, .form-group__form-control:focus {
    border-color: #7f7f7f; }

.form-group textarea.form-group__form-control {
  min-height: 75px;
  resize: none; }

.form-group__help-block {
  font: 14px "Tahoma";
  border-radius: 3px;
  padding: 10px 10px 7px 10px;
  line-height: 150%;
  margin: 10px 0 15px 0; }
  .form-group__help-block--error {
    background-color: #f9e0e0; }
  .form-group__help-block--success {
    background-color: #ddeece; }

.form-group__help-block-header {
  font: 14px "Tahoma";
  display: block;
  margin-bottom: 7px;
  line-height: 100%; }

.form-group__help-block--error .form-group__help-block-header {
  color: #c80000; }

.form-group__help-block--success .form-group__help-block-header {
  color: #3a750b; }

.quote__header {
  color: #000;
  font: 13px "Soho Gothic Pro";
  font-weight: 700; }

.quote__subheader {
  color: #646464;
  font: 11px "Soho Gothic Pro";
  line-height: 200%;
  font-weight: 200; }

.quote__body {
  padding-left: 40px;
  position: relative;
  font: 12px "Tahoma";
  color: #000;
  padding-top: 10px;
  line-height: 150%;
  margin: 5px 0 15px 0; }
  .quote__body:before {
    content: "“";
    color: #b2b2b2;
    font: 57.49px "Stencil D";
    position: absolute;
    top: 0;
    left: -2px;
    width: 40px;
    line-height: 50%; }

.quote__link {
  text-align: right; }
  .quote__link, .quote__link .button {
    font: 12.59px "Soho Gothic Pro";
    line-height: 200%; }
