@import "fonts";
@import "_colors";
@import "_mixins";

body {
  padding: 0;
  margin: 0;
  font: 15px "Soho Gothic Pro", Helvetica, Arial, sans-serif;
}

a {
  color: $main-blue;
}

.page {
  padding: 30px 0;
  background-color: #fff;
  font-family: "Soho Gothic Pro";
  font-size: 15px;
}

.page-paragraph {
  font: 15px "Soho Gothic Pro";
  font-weight: 400;
  margin: 15px 0;
  line-height: 120%;
  color: #333;
}
.page-lead {
  @extend .page-paragraph;
  font: 16.59px "Soho Gothic Pro Medium";
  text-align: center;
  padding: 25px 25px;
  color: #333;
}
.page-callout {
  margin: 0 35px;
  border-radius: 3px;
  padding: 20px;
  margin: 15px 0;
  background-color: #4e97c9;
  font: 16.59px "Soho Gothic Pro Medium";
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.45);
  text-align: center;
}
.page-phone-number {
  text-decoration: none;
  font: 16.59px "Soho Gothic Pro Medium";
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.45);
  padding: 0 5px;
  margin: 0 5px;
  box-shadow: 0px 1px 1px 1px rgba(0,0,0,0.25);
  border-radius: 3px;
  @include linearGradient($button_normal_gradient_top_color, $button_normal_gradient_bottom_color);

  &__prefix {
    font: 12.59px "Soho Gothic Pro Medium";
  }
}

.page-container {
  margin: 0 auto;
  width: 916px;
  padding: 0 30px;

  @media screen and (max-width: 984px) {
    width: 900px;
  }

  @for $i from 1 through length($page_widths) - 1 {
    $w: nth($page_widths, $i);
    $next_w: nth($page_widths, $i + 1);
    $next_w_max: $w - 1;

    @media screen and (max-width: #{$next_w_max}px) {
      width: #{$next_w}px;
    }

    @at-root .w#{$w} & {
      width: #{$next_w}px!important;
    }
  }

  @media screen and (max-width: 399px) {
    padding: 0 15px;
  }

  @media screen and (max-width: 279px) {
    width: 280px;
    padding: 2px;
  }
}